// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import { Button, Input, notification, message, Icon, Spin, Alert, Progress, Dropdown, Menu} from 'ant-design-vue'
import router from './router'
import Check from './components/check/Check.vue'
import Loading from './components/loading/'

import home from './pages/Home/home.vue'
import chatWindow from './pages/ChatWindow/chatWindow.vue'
import chatList from './pages/ChatList/chatList.vue'
import settings from './pages/Settings/settings.vue'

//import 'lib-flexible'
import 'vue2-animate/dist/vue2-animate.min.css';
//import animated from 'animate.css'

Vue.config.productionTip = false

Vue.prototype.$notification = notification;
Vue.prototype.$message = message;

//Vue.use(animated);
Vue.component(Alert.name, Alert);
Vue.component(Check.name, Check);
Vue.component(Button.name, Button);
Vue.component(Input.name, Input);
Vue.component(Icon.name, Icon);
Vue.component(Spin.name, Spin);
Vue.component(Progress.name, Progress);
Vue.component(Dropdown.name, Dropdown);
Vue.component(Menu.name, Menu);
Vue.component(Menu.Item.name, Menu.Item);

Vue.component(home.name, home);
Vue.component(chatWindow.name, chatWindow);
Vue.component(chatList.name, chatList);
Vue.component(settings.name, settings);
Vue.use(Loading);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



