<template>
  <div class="homepage-hero-module">
    <canvas style="
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
      ">
    </canvas>
    <Check v-if="isWarn" @callBack="IsWarn"></Check>
    <div v-else>
      <Loading v-if='loadding' :load='l_loadding'/>
      <transition name="fade" enter-active-class="fadeIn">
      <div class="main" v-show='!loadding'>
        <!-- 顶部导航栏 -->
        <div id="header" v-show="isPass === 4">
          <div class="fr" id="user">
            <div style="height: auto;">
              <transition enter-active-class="fadeIn" leave-active-class="fadeOut">
              <div id="userShow" v-if="userShow" @mouseenter="userShowIn" @mouseleave="userShowOut">
                <div class="logout" title="注销" @mouseenter="logoutSelect" @mouseleave="logoutSelect" @click="Logout">
                  <img v-show="!logoutTouch" style="width: 80%; height: 80%;" src="@/pictures/icon/logout.png">
                  <img v-show="logoutTouch" style="width: 80%; height: 80%;" src="@/pictures/icon/logout_touch.png">
                </div>
              </div>
              </transition>
              <div style="
                    position: absolute;
                    left: 20%;
                    top: 50%;
                    margin-left: -24px;
                    margin-top: -24px;
                    width: 48px;
                    height: 48px;
                    background-size: contain;
                    border-radius: 50%;
                    cursor: pointer;
                    z-index: 100;
              " id="user_icon" @mouseenter="userSelectIn" @mouseleave="userSelectOut">
              <img style="width: 100%; height: 100%; border-radius: 50%;" src="@/pictures/profiles/01.jpg">
              </div>
              <a-dropdown style="
                    position: absolute;
                    left: calc(20% + 24px + 3%);
                    top: 50%;
                    margin-top: -15px;
                    width: 100px;
                    height: 30px;
                    line-height: 30px;
                    font-size: 15px
              ">
                <div class="menuItemStyle">
                  <div style="
                          margin-left: 17px;
                          margin-right: 11px;
                          width: 15px; 
                          height: 15px; 
                          border-radius: 50%; 
                          float: left;
                        " 
                        :class="{'green':(statusSelect === 1), 'yellow':(statusSelect === 2), 'red':(statusSelect === 3)}"></div>
                  <a class="fl">{{statusCorrect}}</a>
                  <a-icon style="margin-left: 5px;" type="down" />
                </div>
                <a-menu class="menuStyle" slot="overlay">
                  <a-menu-item class="menuItemStyle" @click="statusChange(1)">
                    <div style="
                          width: 15px; 
                          height: 15px; 
                          border-radius: 50%; 
                          background-color: green;" 
                        class="fl"></div>
                    <a style="margin-left: 1px;" class="fl">在线</a>
                  </a-menu-item>
                  <a-menu-item class="menuItemStyle" @click="statusChange(2)"> 
                    <div style="
                          width: 15px; 
                          height: 15px; 
                          border-radius: 50%; 
                          background-color: yellow;" 
                        class="fl"></div>
                    <a style="margin-left: 1px;" class="fl">等待</a>
                  </a-menu-item>
                  <a-menu-item class="menuItemStyle" @click="statusChange(3)">
                    <div style="
                          width: 15px; 
                          height: 15px; 
                          border-radius: 50%; 
                          background-color: red;" 
                        class="fl"></div>
                    <a style="margin-left: 1px;" class="fl">离线</a>
                  </a-menu-item>
                </a-menu>
              </a-dropdown>
            </div>
          </div>
        </div>
        <!--  -->

        <!-- 右边导航栏
        <transition-group enter-active-class="fadeInRightBig" leave-active-class="fadeOutRightBig">
        <div v-show="isPass === 4" title="个人界面" class="navigation person" @mouseenter="personSelect" @mouseleave="personSelect" key="person">
          <img v-show="!personTouch" 
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -25%;"
               src="@/pictures/icon/person.png" 
               alt=""
               @load="preload(2)"/>
          <img v-show="personTouch"
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -25%;"
               src="@/pictures/icon/person_touch.png" 
               alt=""
               @load="preload(2)"/>
        </div>
        <div v-show="isPass === 4" title="设置" class="navigation settings" @mouseenter="settingsSelect" @mouseleave="settingsSelect" key="settings">
          <img v-show="!settingsTouch" 
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -20%;"
               src="@/pictures/icon/settings.png" 
               alt=""
               @load="preload(2)"/>
          <img v-show="settingsTouch"
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -20%;"
               src="@/pictures/icon/settings_touch.png" 
               alt=""
               @load="preload(2)"/>
        </div>
        <div v-show="isPass === 4" title="好友" class="navigation friends" @mouseenter="friendsSelect" @mouseleave="friendsSelect" key="friends">
          <img v-show="!friendsTouch" 
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -20%;"
               src="@/pictures/icon/friends.png" 
               alt=""
               @load="preload(2)"/>
          <img v-show="friendsTouch"
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -20%;"
               src="@/pictures/icon/friends_touch.png" 
               alt=""
               @load="preload(2)"/>
        </div>
        <div v-show="isPass === 4" title="注销" class="navigation logout" @mouseenter="logoutSelect" @mouseleave="logoutSelect" @click="Logout" key="logout">
          <img v-show="!logoutTouch" 
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -20%;"
               src="@/pictures/icon/logout.png" 
               alt=""
               @load="preload(2)"/>
          <img v-show="logoutTouch"
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -20%;"
               src="@/pictures/icon/logout_touch.png" 
               alt=""
               @load="preload(2)"/>
        </div>
        </transition-group> -->

        <!-- 左边导航栏
        <transition-group enter-active-class="fadeInLeftBig" leave-active-class="fadeOutLeftBig">
          <div v-show="isPass === 4" title="游戏列表" class="navigation game" @mouseenter="gameSelect" @mouseleave="gameSelect" key="game">
            <img v-show="!gameTouch" 
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -20%;"
               src="@/pictures/icon/add.png" 
               alt=""
              />
            <img v-show="gameTouch"
               style="position: absolute; width: 50%; height: 50%; left: 50%; top: 50%; margin-left: -25%; margin-top: -20%;"
               src="@/pictures/icon/add_touch.png" 
               alt=""
              />
          </div>

          <div v-show="isPass === 4" class="chatListShow" key="chatListShow" @click="chatList"></div>
        </transition-group>

        <transition enter-active-class="fadeInLeft" leave-active-class="fadeOutLeft">
        <chatList v-show="chatListShow === true" class="list"></chatList>
        </transition> -->

        <transition-group mode="out-in" enter-active-class="fadeIn" leave-active-class="fadeOut">
          <div v-show="!(isPass === 4)" key="login_1">  
            <!-- 这里设置两个video的目的是消除高斯模糊后白边的出现 -->
              <!-- <video class="bgPicture bg_blur" muted autoplay loop @canplaythrough="canplay" 
                     @progress="V_progress" ref="backgroundVideo">
                <source src="@/video/mp4/login.mp4" type="video/mp4"/>
              </video>
              <div class="v-loadding" v-show='v_loadding'>
                <a-spin style="margin-right: 5%;" size="large"/>
                <span>视频加载中...</span>
              </div> -->
              <div style="z-index: 0;" class="poster hidden" v-if="!vedioCanPlay">
                  <img class="bgPicture" src="@/pictures/background/01.jpg" alt="" @load="preload(53)">
              </div>
          </div>
          <div style="z-index: 0;" class="poster hidden" v-show="isPass === 4" key="login_2" >
            <img class="bgPicture_in" src="@/pictures/background/04.jpg" alt="" @load="preload(25)">
          </div>
          
          <!-- 这部分是登陆窗口 --> 
          <div class="loginBox" v-show="(isPass === 1)||(isPass === 2)" key="loginBox">
            <div  style="width: 100%; 
                        height: 0;
                        padding-bottom: 100%;
                        background-color:rgba(50, 50, 50, 0.8);
                        z-index:-1;
                        -moz-border-radius: 5px;
                        -webkit-border-radius: 5px;
                        border-radius: 5px;
                        animation-duration: 200ms;
                        animation-delay: 200ms;
            ">
              <div v-show="isPass === 1">
                <img :style="transfer" src="@/pictures/icon/logo.png" alt='' @load="preload(22)">
                <div>
                  <a-input size="large" class="inputStyleLogin" style="top:35%;left:10%;" placeholder="请输入用户名" v-model="id" ref="userInput" key="id" @keyup.enter="Next(1)">
                    <a-icon slot="prefix" type="user" key="id"/>
                    <a-icon v-if="id" slot="suffix" type="close-circle" @click="emitEmpty(1)" key="id"/>
                  </a-input>
                </div>
                <div>
                  <a-input size="large" class="inputStyleLogin" style="top:50%;left:10%;" placeholder="请输入密码" v-model="password" ref="passwordInput" key="password" @keyup.enter="Send">
                    <a-icon slot="prefix" type="unlock" key="password"/>
                    <a-icon v-if="password" slot="suffix" type="close-circle" @click="emitEmpty(2)" key="password"/>
                  </a-input>
                </div> 
                <a-button class="loginButton" style="top:65%;left:20%;" ghost @click="Send">登陆</a-button>
                <a-button class="loginButton" style="top:78%;left:20%;" ghost @click="Register">注册</a-button>
              </div>
              <div v-show="isPass === 2">
                <div v-show="loadding_box">
                  <a-spin class="Centre">
                    <a-icon slot="indicator" type="loading" style="font-size: 10vh;" spin />
                  </a-spin>
                </div>
              </div>
            </div>
          </div>
          <!--  -->

          <!-- 这部分是注册窗口 -->
          <div class="bgPicture" v-show="isPass === 3" key="page_1">
            <div class="bgPicture bg_blur" stlye="animation-duration: 200ms;"></div>
            <div class="registerBox">
              <div  style="
                          width: 100%; 
                          height: 0;
                          padding-bottom: 100%;
                          background-color:rgba(50, 50, 50, 0.8);
                          z-index: -1;
                          -moz-border-radius: 5px;
                          -webkit-border-radius: 5px;
                          border-radius: 5px;
                          animation-duration: 200ms;
                          animation-delay: 200ms;
              ">
                <div>
                  <a-input class="inputStyleRegister" style="top:0;left:10%;" placeholder="请输入用户名" v-model="re_id" ref="re_userInput" key="id" @keyup.enter="reNext(1)">
                    <a-icon slot="prefix" type="user" key="id"/>
                    <a-icon v-if="re_id" slot="suffix" type="close-circle" @click="re_emitEmpty(1)" key="id"/>
                  </a-input>
                </div>
                <div>
                  <a-input class="inputStyleRegister" style="top:13%;left:10%;" placeholder="请输入密码" v-model="re_password" ref="re_passwordInput" key="password" @keyup.enter="reNext(2)">
                    <a-icon slot="prefix" type="unlock" key="password"/>
                    <a-icon v-if="re_password" slot="suffix" type="close-circle" @click="re_emitEmpty(2)" key="password"/>
                  </a-input>
                </div>
                <div>
                  <a-input class="inputStyleRegister" style="top:26%;left:10%;" placeholder="请输入Origin账号" v-model="re_origin" ref="re_originInput" key="origin_id" @keyup.enter="reNext(3)">
                    <a-icon slot="prefix" type="paper-clip" key="origin_id"/>
                    <a-icon v-if="re_origin" slot="suffix" type="close-circle" @click="re_emitEmpty(3)" key="origin_id"/>
                  </a-input>
                </div>
                <div>
                  <a-input class="inputStyleRegister" style="top:39%;left:10%;" placeholder="请输入邮箱 ( 用于找回密码 )" v-model="re_mail" ref="re_mailInput" key="re_mail" @keyup.enter="reNext(4)">
                    <a-icon slot="prefix" type="mail" key="re_mail"/>
                    <a-icon v-if="re_mail" slot="suffix" type="close-circle" @click="re_emitEmpty(4)" key="re_mail"/>
                  </a-input>
                </div>
                <div>
                  <a-input class="inputStyleRegister" style="top:52%;left:10%;" placeholder="请输入电话号码" v-model="re_phone" ref="re_phoneInput" key="re_phone" @keyup.enter="reServer">
                    <a-icon slot="prefix" type="phone" key="re_phone"/>
                    <a-icon v-if="re_phone" slot="suffix" type="close-circle" @click="re_emitEmpty(5)" key="re_phone"/>
                  </a-input>
                </div>
                <a-button class="registerButton" style="top:65%;left:20%;" ghost @click="reServer">注册</a-button>
                <a-button class="registerButton" style="top:78%;left:20%;" ghost @click="Back">返回</a-button>
              </div>
            </div>
          </div>
          <!--  -->
          <div v-show="isPass === 4" class="home" key="page_2">
              <home v-show="isPass === 4" :ishome=isPass @homeLoadding="Tohome"></home>
              <!-- <chatWindow  @chat_complete="chatComplete" @backToLogin="BackToLogin"></chatWindow> -->
          </div>
        </transition-group>
      </div>
      </transition>
    </div>
  </div>
</template>
<script>
import { setTimeout } from 'timers';

export default {
    name: 'login',
    data() {
        return{
            loadding: true,//页面加载标识
            isWarn: true,//浏览器版本警告标识
            vedioCanPlay: false,//背景视频播放标识
            v_loadding: true,//背景视频加载标识是否显示
            l_loadding: 0,//登陆页面图片加载进度 
            chat_complete: false,//聊天页面资源是否加载完成
            fixStyle: '',//自适应样式
            id: '',//登陆窗口用户名
            password: '',//登陆窗口密码
            re_id: '',//注册用户名
            re_password: '',//注册密码
            re_origin: '',//注册关联的origin账号
            re_mail: '',//注册邮箱
            re_phone: '',//注册电话
            transfer: {
              'display': 'inline-block', 
              'width': '30%',
              'margin-top': '5%',
            },
            isPass: 1,//登陆窗口切换标识 1:登陆窗口 2:加载中 3:注册窗口 4:聊天页面
            loadding_box: true,//加载中标识

            userSelect: false,//鼠标是否放在头像上
            userShowSelect: false,//鼠标是否放在缩略版个人界面上
            userShow: false,//是否显示缩略版个人界面

            statusSelect: 1,//用户当前的状态种类
            status: ['在线','等待','离线'],//状态种类
            statusCorrect: '在线',//当前状态

            personTouch: false,//鼠标是否在个人信息上
            settingsTouch: false,//鼠标是否在设置上
            friendsTouch: false,//鼠标是否放在好友列表上
            logoutTouch: false,//鼠标是否放在注销上
            gameTouch: false,//鼠标是否放在左侧导航栏的游戏列表上
            chatListShow: false,//是否展开聊天列表

            homeLoadding: false,//首页是否加载完成标识
        }
    },
    mounted: function(){
        var _this = this;
        //变换浏览器窗口时改变显示大小
        // window.onresize = () => {
        // const windowWidth = document.body.clientWidth;
        // const windowHeight = document.body.clientHeight;
        // const windowAspectRatio = windowHeight / windowWidth
        // let videoWidth
        // let videoHeight
        // if (windowAspectRatio < 0.5625) {
        //   videoWidth = windowWidth
        //   videoHeight = videoWidth * 0.5625
        //   this.fixStyle = {
        //     height: windowWidth * 0.5625 + 'px',
        //     width: windowWidth + 'px',
        //     'margin-bottom': (windowHeight - videoHeight) / 2 + 'px',
        //     'margin-left': 'initial',
        //   }
        // } else {
        //   videoHeight = windowHeight
        //   videoWidth = videoHeight / 0.5625
        //   this.fixStyle = {
        //     height: windowHeight + 'px',
        //     width: windowHeight / 0.5625 + 'px',
        //     'margin-left': (windowWidth - videoWidth) / 2 + 'px',
        //     'margin-bottom': 'initial',
        //     }
        //   }
        // }
        // window.onresize();
    },
    watch:{
      //监听登陆页面加载完成
      "l_loadding": function(){
        var _this = this;
        if(this.l_loadding === 100){
          setTimeout(
            function(){
              _this.loadding = false;
            },
            1000
          );
        }
      },
      "homeLoadding": function(){
        var _this = this;
        if((this.homeLoadding === true)&&(this.isPass === 2)){
          setTimeout(
            function(){
               _this.isPass = 4;
            },1000
          )
        }
      },
      //聊天页面加载完成触发
      "chat_complete": function(){
        var _this = this;
        if(this.chat_complete && (this.isPass === 2)){
          _this.isPass = 4;
          this.$notification.config({
            placement: 'bottomRight',
            bottom: '1vh',
            duration: 2,
          });
          this.$notification.open({
            message: '登陆信息',
            description: '欢迎您，亲爱的xxx',
          });
        }
      }
    },
    methods: {
        //登陆页面加载标识
        preload: function(e){
          var _this = this;
          var num = this.l_loadding;
          for(var i = num; i < num + e; i++){
            setTimeout(
              function(){
                _this.l_loadding += 1;
              },100
            );
          }
        },
        //控制背景视频播放的方法，当播放出现问题显示静态图片
        canplay: function() {
            this.vedioCanPlay = true;
        },
        //发送登陆信息
        Send: function(){
          var _this = this;
          this.isPass = 2;
          if(this.homeLoadding === true){
            setTimeout(
              function(){
                _this.isPass = 4;
                // _this.$refs.backgroundVideo.pause();
                console.log(1);
              },1000
            )
          }
        },
        //首页是否加载完成
        Tohome: function(e){
          this.homeLoadding = e;
        },
        //切换至注册页面
        Register: function(){
          this.isPass = 3;
        },
        Logout: function(){
          // this.$refs.backgroundVideo.play();
          this.isPass = 1;
          this.$notification.config({
                placement: 'bottomRight',
                bottom: '1vh',
                duration: 2,
          });
          this.$notification.open({
                message: '登出信息',
                description: '您已登出',
          });
        },
        //清空输入框中的值
        emitEmpty: function(num) {
          if (num === 1){
            this.$refs.userInput.focus()
            this.id = ''
          }else{
            this.$refs.passwordInput.focus()
            this.password = ''
          }
        },
        //返回清空注册页面的信息
        re_emitEmpty: function(num){
          if (num === 1){
            this.$refs.re_userInput.focus()
            this.re_id = ''
          }else if(num === 2){
            this.$refs.re_passwordInput.focus()
            this.re_password = ''
          }else if(num === 3){
            this.$refs.re_originInput.focus()
            this.re_origin = ''
          }else if(num === 4){
            this.$refs.re_mailInput.focus()
            this.re_mail = ''
          }else{
            this.$refs.re_phoneInput.focus()
            this.re_phone = ''
          }
        },
        //登陆页面跳转至下一个输入框
        Next: function(num){
          if(num === 1){
            this.$refs.passwordInput.focus();
          }
        },
        //注册页面跳转至下一个输入框
        reNext: function(num){
          if(num === 1){
            this.$refs.re_passwordInput.focus();
          }else if(num === 2){
            this.$refs.re_originInput.focus();
          }else if(num === 3){
            this.$refs.re_mailInput.focus();
          }else if(num === 4){
            this.$refs.re_phoneInput.focus();
          }
        },
        //注册信息发送
        reServer: function(){

        },
        //注册页面返回登陆页面
        Back: function(){
          this.isPass = 1;
          this.re_id = '';
          this.re_password = '';
          this.re_origin = '';
          this.re_mail = '';
          this.re_phone = '';
        },
        //确认聊天界面加载成功
        chatComplete: function(e){
          this.chat_complete = e;
        },
        //是否进行浏览器检测警告
        IsWarn: function(e){
          this.isWarn = e;
        },
        BackToLogin: function(e){
          this.$refs.backgroundVideo.play();
          this.isPass = e;
        },
        V_progress: function(){
          var _this = this;
          this.v_loadding = true; 
          setTimeout(function(){
            _this.v_loadding = false;
          }, 1000);
        },

        referenceSelect: function(){
          this.referenceTouch = !this.referenceTouch;
        },
        forumSelect: function(){
          this.forumTouch = !this.forumTouch;
        },
        newsSelect: function(){
          this.newsTouch = !this.newsTouch;
        },
        settingsSelect: function(){
          this.settingsTouch = !this.settingsTouch;
        },
        personSelect: function(){
          this.personTouch = !this.personTouch;
        },
        friendsSelect: function(){
          this.friendsTouch = !this.friendsTouch;
        },
        logoutSelect: function(){
          this.logoutTouch = !this.logoutTouch;
        },
        gameSelect: function(){
          this.gameTouch = !this.gameTouch;
        },
        chatList: function(){
            this.chatListShow = !this.chatListShow;
        },
        userSelectIn: function(){
          this.userSelect = true;
          this.userShow = true;
        },
        userSelectOut: function(){
          var _this = this;
          this.userSelect = false;
          setTimeout(function(){
            if(!(_this.userShowSelect)) {_this.userShow = false;}
          },10);
        },
        userShowIn: function(){
          this.userShowSelect = true;
        },
        userShowOut: function(){
          var _this = this;
          this.userShowSelect = false;
          setTimeout(function(){
            if(!(_this.userSelect)) {_this.userShow = false;}
          },10);
        },
        statusChange: function(e){
          var _this = this;
          this.statusSelect = e;
          this.statusCorrect = this.status[_this.statusSelect - 1];
        }
    }
}
</script>
<style>
@import "..\css\login\login_1920.css";
</style>



