<template>
    <div class="home_in">
        <div class="gameList">
            <div>
                <div class="gameList_up">
                    <div style="width: 100%; height: 100%;" class="filter"></div>
                    <img src="@/pictures/game/01.jpg" alt="">
                </div>
                <div class="gameSelect">
                    <img src="@/pictures/game/02.jpg" alt="">
                </div>
                <div class="gameList_down">
                    <div style="width: 100%; height: 100%;" class="filter"></div>
                    <img src="@/pictures/game/03.jpg" alt="">
                </div>
            </div>
        </div>
        <div class="itemList">
            <div class="topic">
                热门话题小组
            </div>
            <div class="union">
                热门工会
            </div>
            <div class="room">
                房间列表
            </div>
            <div class="match">
                匹配模式
            </div>
        </div>
        <div class="mainPage">
            <div class="topBar">
                <div style="font-size: 60px; color: white; left: 10px;">资讯</div>
                <div style="font-size: 40px; color: white; left: 150px;">攻略</div>
                <div style="font-size: 40px; color: white; left: 240px;">聊天</div>
            </div>
            <div class="chatWin">

            </div>
        </div>
        <div class="messageList">
            <div class="messageItems">
                消息列表
            </div>
            <div class="friendList">
                好友列表
            </div>
        </div>
        <!-- <div class="swiper">
            <div class="swiper-container" @mouseenter="swiperSelect" @mouseleave="swiperSelectOut">
                <div class="swiper-wrapper">
                    <div class="swiper-slide"><img src="@/pictures/background/01.jpg" @load="preload(25)"></div>
                    <div class="swiper-slide"><img src="@/pictures/background/02.jpg" @load="preload(25)"></div>
                    <div class="swiper-slide"><img src="@/pictures/background/03.jpg" @load="preload(25)"></div>
                    <div class="swiper-slide"><img src="@/pictures/background/04.jpg" @load="preload(25)"></div>
                </div>
            </div>
            <transition leave-active-class="fadeOut">
            <div class="swiper-pagination" v-show="paginationShow" @mouseenter="swiperSelect" @mouseleave="swiperSelectOut"></div>
            </transition>
        </div>
        <div class="buttons">
            <div class="buttonArea">
                <button class="homeButton">
                    公开世界
                </button>
            </div>
            <div class="buttonArea">
                <button class="homeButton">
                    选择游戏
                </button>
            </div>
            <div class="buttonArea">
                <button class="homeButton">
                    资讯频道
                </button>
            </div>
        </div> -->
    </div>
</template>
<script>
import Swiper from 'swiper';
import "swiper/dist/css/swiper.min.css";
import { setTimeout } from 'timers';
export default {
    name: "home",
    props:['ishome'],
    data() {
        return{
            loadding: 0,
            paginationShow: false,
        }
    },
    // watch:{
    //     "ishome": function(){
    //         var _this = this;
    //         if(this.ishome === 4){
    //             _this.swiper_init();
    //         }
    //     }
    // },
    mounted(){
        var _this = this;
        this.preload(100);
    },
    methods:{
        preload: function(e){
            var _this = this;
            this.loadding += e;
            if(this.loadding === 100){
                _this.$emit('homeLoadding', true);
            }
        },
        swiper_init: function(){
            var swiperContianer = new Swiper('.swiper-container', {
                    loop: true,        
                    direction: 'horizontal', //切换方向
                    effect: 'slide', //切换特效
                    observer: true,
                    observerParents: true,
                    observeSlideChildren: true,
                    pagination: {
                        el: '.swiper-pagination',
                        clickable :true,
                        renderBullet: function (index, className) {
			                return  '<span class="'+className+'"><img src="static/img/0' + (index + 1) + '.jpg"></span>';
		    	        },
                    },
                    // navigation: {
                    //     nextEl: '.swiper-button-next',
                    //     prevEl: '.swiper-button-prev',
                    // },
                    autoplay: true,
                    autoplay:{
                        disableOnInteraction: false,
                        delay: 3000,
                    },
                });
            swiperContianer.pagination.render();
            swiperContianer.autoplay.start();
        },
        swiperSelect: function(){
            this.paginationShow = true;
        },
        swiperSelectOut: function(){
            this.paginationShow = false;
        }
    }    
}
</script>
<style>
.home_in{
    position: relative;
    width: 100%;
    height: 100%;
}

.gameList{
    float: left;
    width: 15%;
    height: 100%;
    border: 1px solid white;
    background-color: rgba(114, 114, 114, 0.5);
    display:flex;justify-content:center;align-items:center;
}

.gameSelect{
    position: relative;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 5px solid yellow;
    margin-top: 40%;
    margin-bottom: 40%;
    cursor:pointer;
}

.gameSelect img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    z-index: 99;
}

.gameList_up{
    position: relative;
    margin:0 auto;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    cursor:pointer;
}

.gameList_up img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    z-index: 99;
}

.gameList_down{
    position: relative;
    margin:0 auto;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    cursor:pointer;
}

.gameList_down img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    z-index: 99;
}

.itemList{
    position: relative;
    float: left;
    width: 25%;
    height: 100%;
    border: 1px solid white;
    background-color: rgba(114, 114, 114, 0.5);
    display:flex;justify-content:center;align-items:center;
}

.match{
    position: absolute;
    bottom: 10px;
    width: 100%;
    height: 100px;
    font-size: 20px;
    line-height: 100px;
    text-align: center;
    color: white;
    border: 1px solid white;
}

.topic{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 20px;
    text-align: center;
    color: white;
    border: 1px solid white;
}

.union{
    position: absolute;
    top: calc(100% - 190px);
    left: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 20px;
    text-align: center;
    color: white;
    border: 1px solid white;
}

.room{
    position: absolute;
    top: calc(100% - 150px);
    left: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 20px;
    text-align: center;
    color: white;
    border: 1px solid white;
}

.mainPage{
    position: relative;
    float: left;
    width: 40%;
    height: 100%;
    border: 1px solid white;
    background-color: rgba(114, 114, 114, 0.5);
    display:flex;justify-content:center;align-items:center;
}

.topBar{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
    border: 1px solid white;
}
.topBar div{
    position:absolute;
    bottom: 0;
    margin-left: 10px;
    cursor: pointer;
}

.chatWin{   
    position: absolute;
    top: 100px;
    width: 100%;
    height: calc(100% - 100px);
    border: 1px solid white;
}

.messageList{
    position: relative;
    float: left;
    width: 20%;
    height: 100%;
    border: 1px solid white;
    background-color: rgba(114, 114, 114, 0.5);
    display:flex;justify-content:center;align-items:center;
}

.messageItems{
    position: absolute;
    top: 0; left: 0;
    width: 100%; height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 20px;
    color: white;
    border: 1px solid white;
}

.friendList{
    position: absolute;
    bottom: 0; left: 0;
    width: 100%; height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 20px;
    color: white;
    border: 1px solid white;
}

.swiper{
    position: absolute;
    left: 50%;
    top: 20px;
    margin-left: -49%;
    width: 98%;
    height: 70%;
    min-height: 385px;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    background-color: rgba(0, 0, 0, 0.8);
}

.swiper-container{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 70%;
    min-height: 385px;
    border-radius: 5px;
    background-color:rgba(0, 0, 0, 0.8);
}

.swiper-pagination{
    position: absolute;
    left: 50%;
    bottom: -2px;
    margin-left: -50%;
    width: 100%;
    height: 30%;
    min-height: 100px;
    border-radius: 0 0 5px 5px;
    background-color: rgba(200, 200, 200, 0.6);
}

.swiper-pagination-bullet{
    width:auto !important;
    height: 100%!important;
    min-height: 100px!important;
    margin-left: 2px;
    margin-right: 2px;
    background:none !important;
    cursor:pointer;
}

.swiper-pagination-bullet img{
    width:auto;
    height: 100%!important;
    min-height: 100px!important;
    cursor:pointer;
}

.swiper-slide {
    border-radius: 5px;
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}

.swiper-slide img {
    max-width: 100%;
    height: auto;
} 

.buttons{
    position: absolute;
    left: 50%;
    bottom: 3%;
    margin-left: -49%;
    width: 98%;
    height: 20%;
    min-height: 110px;
    font-size: 15px;
    border-radius: 5px;
    border: 1px solid rgba(0, 0, 0, 0.2);
}

.buttonArea{
    display: inline-block;
    width: 33%;
    height: 100%;
    line-height: 100%;
}

.homeButton{
    margin-left: 10%;
    width: 80%;
    height: 100%;
}

.filter{
  z-index: 100;
  position: absolute;
  border-radius: 50%;
  background-color: rgba(75, 75, 75, 0.5);
}
</style>



